
<template>
  <div style="height:100%;box-sizing:border-box;padding-top:120px;background-color:#2ba06a;">
    <div class="outer_label">
      <img
        class="inner_label login_logo"
        src="../../assets/img/icon/logo@3x.png"
      />
      <p class="flex-horizontal" style="color:#fff;margin-top:10px;font-size:18px;">泰瑞5G智慧农业</p>
    </div>
    <div class="flex-center flex-column" style="margin-top:16px;">
      <div style="position:relative;">
        <div class="form-field flex-between flex-vertical" style="width:287px;margin-bottom:16px;">
          <van-icon name="manager" color="#fff" size="14"/>
          <input
            placeholder="用户名"
            v-model="userName"
          />
        </div>
      </div>
      <div class="form-field flex-between flex-vertical" style="width:287px;margin-bottom:16px;">
        <van-icon name="bell" color="#fff" size="14"/>
        <input
          type="password"
          placeholder="密码"
          v-model="password"
        />
      </div>
    </div>

    <div style="text-align:center;margin-bottom:16px;">
      <van-button plain hairline color="#253238" @click="_login" round >登录</van-button >
    </div>

    <div class="flex-between flex-vertical" style="padding:0 40px;">
      <router-link to="/register" style="color:#fff;font-size:12px;">还没有账号？</router-link>
      <router-link to="/forgetPswd" style="color:#fff;font-size:12px;">找回密码</router-link>
    </div>
  </div>
</template>
<script>
import { login, getPersonalInfo } from '@/api';
export default {
  name: "",
  data() {
    return {
      userName: "",
      password: "",
      isBtnLoading: false
    };
  },
  computed: {
    btnText() {
      if (this.isBtnLoading) return "登录中...";
      return "登录";
    },
  },
  created() {
    this.checkLogin();
  },
  mounted(){
    document.title = '登录';
    this.fillRemberUser();
  },
  methods: {

    /**
     * 检查登录状态
     * 如果登录状态有效，则返回上一个页面
     */
    checkLogin(){
      let token = sessionStorage.getItem('token') || localStorage.getItem('token');
      if(token){
        sessionStorage.setItem('token',token);
        getPersonalInfo().then(res=>{
          if (res.code == 0) {
            sessionStorage.setItem("userId", res.data.userCode);
            sessionStorage.setItem("userShop", res.data.userShop);
            sessionStorage.setItem("shopCode", res.data.shopCode);
            sessionStorage.setItem(
              "headimgurl",
              this.$utils.dealImg(res.data.userHeader)
            );
            sessionStorage.setItem("userName", res.data.userName);
            sessionStorage.setItem("userName2", res.data.userName2 || "");
            sessionStorage.setItem("userMobile2", res.data.userMobile2 || "");

            let hash = sessionStorage.getItem('hash') || '/';
            this.$router.push(hash);
          }
        })
        
      }
    },
    /**
     * 自动填充记住的用户名和密码
     */
    fillRemberUser(){
      let userName = localStorage.getItem('myLoginUserName');
      let password = localStorage.getItem('myLoginPassword');
      if(userName !== null && password !== null){
        this.userName = userName;
        this.password = password;
      }
    },
    _login() {
      if (!this.userName) {
        this.$toast.fail("请输入用户名");
        return;
      }
      if (!this.password) {
        this.$toast.fail("请输入密码");
        return;
      }

      login({
        account_number: this.userName,
        code: this.password,
        isCookie: true,
        openid: ''
      }).then(res=>{
        if(res.code === 0){

          localStorage.setItem('token',res.data.token);
          sessionStorage.setItem("token", res.data.token);
          sessionStorage.setItem("userId", res.data.userCode);
          sessionStorage.setItem("userShop", res.data.userShop);
          sessionStorage.setItem("shopCode", res.data.shopCode);
          sessionStorage.setItem(
            "headimgurl",
            this.$utils.dealImg(res.data.userHeader)
          );
          sessionStorage.setItem("userName", res.data.userName);
          sessionStorage.setItem("userName2", res.data.userName2 || "");
          sessionStorage.setItem("userMobile2", res.data.userMobile2 || "");

          // 记住用户名密码
          localStorage.setItem('myLoginUserName',this.userName);
          localStorage.setItem('myLoginPassword',this.password);

          this.$router.push('/');
          this.$toast.success(res.msg);
        }else{
          this.$toast.fail(res.msg);
        }
      })
    }
  },
};
</script>
